.breadcrumb-outer-wrapper {
    background: $purple_light;
    overflow: hidden;
    position: relative;
    z-index: 10;
}

.breadcrumb-outer-wrapper::before {
    display: inline-block;
    content: ' ';
    background: $purple;
    height: 100%;
    width: 50%;
    position: absolute;
    left: 0;
    top: 0;
}

@media only screen and (max-width: 1080px) {

    .breadcrumb-outer-wrapper {
        display: none;
    }
}

.breadcrumb-outer-wrapper .container {
    background: #fff;
    position: relative;
}

.breadcrumb {
    list-style: none;
    display: table;
    width: 100%;
    background: $white;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex; /* li */
}

.breadcrumb li {
    float: left;
    font-family: 'Open sans'; /* a */
}

.breadcrumb li:last-child {
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    background: $purple_light;
    position: relative;
}

.breadcrumb li:last-child::before {
    content: '';
    position: absolute;
    top: 0;
    border: 0 solid $purple_light;
    border-width: 26px 10px;
    width: 0;
    height: 0;
}

.breadcrumb li:last-child::before {
    left: -20px;
    border-left-color: transparent;
}

.breadcrumb li:nth-last-child(2) a {
    margin-right: 5px;
    background: $purple_light;
}

.breadcrumb li:nth-last-child(2) a::before {
    border-color: $purple_light;
    border-left-color: transparent;
}

.breadcrumb li a {
    color: $white;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    background: $purple;
    text-decoration: none;
    position: relative;
    height: 50px;
    line-height: 40px;
    padding: 0 10px 0 5px;
    text-align: center;
    margin-right: 23px;
}

.breadcrumb li a::before,
            .breadcrumb li a::after {
    content: '';
    position: absolute;
    top: 0;
    border: 0 solid $purple;
    border-width: 26px 10px;
    width: 0;
    height: 0;
}

.breadcrumb li a::before {
    left: -20px;
    border-left-color: transparent;
}

.breadcrumb li a::after {
    left: 100%;
    border-color: transparent;
    border-left-color: $purple;
}

.breadcrumb li a:hover {
    background-color: $purple_light;
}

.breadcrumb li a:hover::before {
    border-color: $purple_light;
    border-left-color: transparent;
}

.breadcrumb li a:hover::after {
    border-left-color: $purple_light;
}

.breadcrumb li a:active {
    background-color: $purple_light;
}

.breadcrumb li a:active::before {
    border-color: $purple_light;
    border-left-color: transparent;
}

.breadcrumb li a:active::after {
    border-left-color: $purple_light;
}

/* breadcrumb */

.breadcrumb .icon::before {
    font-size: 36px;
    vertical-align: bottom;
    display: inline-block;
}
